"use client";

import { useState } from 'react';
import CounterButton from '../components/CounterButton';
import DarkModeToggle from '../components/DarkModeToggle';
import useDarkMode from '../lib/useDarkMode';

const Page = () => {
  const [count, setCount] = useState(0);
  const { isDarkMode, toggleDarkMode } = useDarkMode();

  return (
    <div className={`flex flex-col items-center justify-center min-h-screen bg-gray-100 dark:bg-black ${isDarkMode ? 'bg-[#2e3440]' : ''}`}>
      <h1 className='text-5xl font-bold mb-8'>Counter App</h1>
      <div className='flex flex-col items-center justify-center space-y-6'>
        <button
          onClick={() => setCount(count - 1)}
          className={`bg-gradient-to-r from-blue-400 to-purple-500 text-white px-8 py-3 rounded-full shadow-lg hover:scale-105 transition-transform ${isDarkMode ? 'text-[#2e3440]' : ''}`}
        >
          -
        </button>
        <div className='bg-gray-200 dark:bg-[#3b4252] backdrop-blur-lg p-8 rounded-full shadow-md'>
          {count}
        </div>
        <button
          onClick={() => setCount(count + 1)}
          className={`bg-gradient-to-r from-green-400 to-teal-600 text-white px-8 py-3 rounded-full shadow-lg hover:scale-105 transition-transform ${isDarkMode ? 'text-[#2e3440]' : ''}`}
        >
          +
        </button>
      </div>
      <DarkModeToggle toggle={toggleDarkMode} isOn={isDarkMode} />
    </div>
  );
};

export default Page;